import React from "react"
import { Link } from "gatsby"
import useBlogData from "../static_queries/useBlogData"
import blogListStyles from "../styles/components/bloglist.module.scss"
import Img from 'gatsby-image'
import { getSessionPassword } from '../helpers/passwordProtectUtils.js'

export default function BlogList(props) {
  // Check if current post's slug was passed in props so to filter out that post in below list
  const curPost = props.curPost ? props.curPost : false;

  // Only use this password check bool for cosmetic uses, not secure.
  const pw = getSessionPassword();
  const isSessionPasswordSet = pw && pw !== '' ? true : false;

  const blogData = useBlogData()
  function renderBlogData() {
    return (
      <div>
        {blogData
          .filter(blog => blog.node.frontmatter.title !== "" && curPost !== blog.node.fields.slug)
          .map(blog => {
            const isProtected = blog.node.fields.encrypted;
            return (
              <Link to={`${blog.node.fields.slug}`} key={blog.node.id}>
                <li className={blogListStyles.li} key={blog.node.fields.slug}>
                  <div className={blogListStyles.list__hero}>
                    <Img 
                      fluid={
                        blog.node.frontmatter.hero_image.childImageSharp.fluid
                      }
                      alt={blog.node.frontmatter.title}
                    />
                  </div>
                  <div className={blogListStyles.list__info}>
                    <h2>{`${isProtected && ! isSessionPasswordSet ? 'Protected: ' : ''}${blog.node.frontmatter.title}`}</h2>
                    <h3>{blog.node.frontmatter.date}</h3>
                    <p>{blog.node.excerpt}</p>
                  </div>
                </li>
              </Link>
            )
          })}
      </div>
    )
  }
  return (
    <section>
      <ul className={blogListStyles.list}>{renderBlogData()}</ul>
    </section>
  )
}

